.tooltip-container {
  margin-left: 5px;
  transform: translateY(-2px);
  display: inline-block; }
  @media screen and (max-width: 575px) {
    .tooltip-container img {
      height: 16px;
      width: 16px; } }

.tooltip-inner-custom {
  transform: translateX(20px) translateY(3px);
  background-color: white;
  border: 2px solid #6480EB;
  box-sizing: border-box;
  border-radius: 12px;
  padding: 25px 20px;
  text-align: left; }
  .tooltip-inner-custom p {
    color: black;
    white-space: pre-wrap; }
  .tooltip-inner-custom .desc-text,
  .tooltip-inner-custom .title-text {
    font-size: 14px;
    color: #2C3652; }
  .tooltip-inner-custom .desc-text {
    font-weight: normal; }
  .tooltip-inner-custom .title-text {
    font-weight: bold; }

.tooltip-arrow-custom:before {
  content: "";
  top: 5px !important;
  width: 12px;
  height: 12px;
  position: absolute;
  transform: rotate(45deg);
  border: 2px solid #6480EB !important;
  border-right-color: transparent !important;
  border-bottom-color: transparent !important;
  border-top-left-radius: 4px;
  z-index: 1;
  background: white; }

.bs-tooltip-top .tooltip-inner-custom {
  transform: translateX(20px) translateY(-2px); }

.bs-tooltip-top .tooltip-arrow-custom:before {
  top: -10px !important;
  transform: rotate(-135deg); }

.tooltip-wide-modal {
  max-width: 300px;
  padding: 20px 15px; }
