.age-group-item-container {
  min-height: 70px;
  height: 100%;
  position: relative;
  background: white;
  border: 1px solid #CADBEA;
  box-sizing: border-box;
  border-radius: 12px;
  padding: 10px 20px;
  display: flex;
  align-items: center; }
  @media screen and (max-width: 575px) {
    .age-group-item-container {
      min-height: 50px;
      padding: 5px 10px; } }
  .age-group-item-container .title {
    flex: 1;
    font-weight: bold;
    font-size: 18px;
    color: #2C3652;
    margin: 0; }
    @media screen and (max-width: 575px) {
      .age-group-item-container .title {
        font-size: 14px; } }
  .age-group-item-container .selector {
    height: 100%;
    max-width: 150px;
    border-radius: 12px; }
  .age-group-item-container .age-group-input {
    height: 100%;
    border: none;
    background-color: transparent;
    font-weight: bold;
    font-size: 16px;
    color: #6480EB;
    border: 1px solid #CADBEA;
    box-sizing: border-box;
    border-radius: 12px; }
    @media screen and (max-width: 575px) {
      .age-group-item-container .age-group-input {
        font-size: 14px; } }
    .age-group-item-container .age-group-input:focus, .age-group-item-container .age-group-input:active {
      box-shadow: none;
      outline: none;
      background-color: transparent; }
    .age-group-item-container .age-group-input::placeholder {
      font-weight: normal;
      font-size: 12px;
      color: #B6BDCB; }
  .age-group-item-container input::-webkit-outer-spin-button,
  .age-group-item-container input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0;
    /* <-- Apparently some margin are still there even though it's hidden */ }
  .age-group-item-container input[type=number] {
    -moz-appearance: textfield;
    /* Firefox */ }
