.selection-container-v3 button {
  width: 100%;
  min-height: 48px;
  border-radius: 12px;
  border: 1px solid #C8E0E4;
  background-origin: border-box;
  background-clip: content-box, border-box;
  color: #46737A;
  font-weight: bold;
  font-size: 12px; }
  .selection-container-v3 button.selected {
    background-image: linear-gradient(#F2FAF9, #F2FAF9), linear-gradient(110deg, #00D7AF, #00D7AF);
    border: solid 2px transparent;
    color: #354F53; }

.selection-container-v3 .col-sm-6 {
  margin-bottom: 10px; }
  @media screen and (min-width: 576px) {
    .selection-container-v3 .col-sm-6 {
      margin-bottom: 0; }
      .selection-container-v3 .col-sm-6:nth-of-type(even) {
        padding-left: 5px; }
      .selection-container-v3 .col-sm-6:nth-of-type(odd) {
        padding-right: 5px; } }

.selection-container-v3 .col-sm-4 {
  margin-bottom: 10px; }
  @media screen and (min-width: 576px) {
    .selection-container-v3 .col-sm-4 {
      margin-bottom: 0; }
      .selection-container-v3 .col-sm-4:nth-of-type(3n+0) {
        padding-left: 5px; }
      .selection-container-v3 .col-sm-4:nth-of-type(3n+1) {
        padding-left: 5px;
        padding-right: 5px; }
      .selection-container-v3 .col-sm-4:nth-of-type(3n+2) {
        padding-right: 5px; } }
