.age-group-margin {
  max-width: 660px;
  margin: auto;
  margin-bottom: 40px; }

@media screen and (max-width: 575px) {
  .age-group-container {
    margin-bottom: 30px; } }

.age-group-container .title-container {
  width: 100%;
  margin-bottom: 40px; }
  @media screen and (max-width: 575px) {
    .age-group-container .title-container {
      margin-bottom: 20px; } }
  .age-group-container .title-container button p {
    margin: 0;
    font-weight: bold;
    font-size: 14px;
    color: #6E7F95; }
    .age-group-container .title-container button p:hover {
      text-decoration: underline; }
    @media screen and (max-width: 575px) {
      .age-group-container .title-container button p {
        font-size: 12px; } }
  .age-group-container .title-container h3 {
    font-weight: bold;
    font-size: 24px;
    text-align: center;
    color: #00D7AF;
    margin: auto; }
    @media screen and (max-width: 575px) {
      .age-group-container .title-container h3 {
        font-size: 14px;
        margin-bottom: 10px; } }
  .age-group-container .title-container h6 {
    text-align: center;
    font-weight: 18px;
    font-size: 14px;
    color: #6E7F95;
    white-space: pre-wrap;
    margin-top: 5px; }
    @media screen and (max-width: 575px) {
      .age-group-container .title-container h6 {
        margin-top: 0;
        transform: translateY(-8px);
        font-size: 10px; } }
  .age-group-container .title-container .employees {
    transform: translateY(5px);
    font-weight: bold;
    font-size: 14px;
    text-align: right;
    color: #2C3652; }
    @media screen and (max-width: 575px) {
      .age-group-container .title-container .employees {
        font-size: 12px; } }
