.image-selection-margin {
  max-width: 960px;
  margin: auto;
  margin-bottom: 40px; }

@media screen and (max-width: 575px) {
  .image-selection-container {
    margin-bottom: 30px; } }

.image-selection-container .title-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px; }
  @media screen and (max-width: 575px) {
    .image-selection-container .title-container {
      margin-bottom: 10px; } }
  .image-selection-container .title-container h3 {
    font-weight: bold;
    font-size: 24px;
    text-align: center;
    color: #00D7AF; }
    @media screen and (max-width: 575px) {
      .image-selection-container .title-container h3 {
        font-size: 14px; } }
  .image-selection-container .title-container .tooltip-container {
    transform: translateX(25px); }
    @media screen and (max-width: 575px) {
      .image-selection-container .title-container .tooltip-container {
        transform: translateX(10px) translateY(-2px); } }

.image-selection-container .options-container {
  max-width: 660px;
  margin: auto; }

.image-selection-container .item-container-wrapper:nth-of-type(even) .item-container-wrapper-inner {
  padding-left: 10px; }

.image-selection-container .item-container-wrapper:nth-of-type(even) .item-container,
.image-selection-container .item-container-wrapper:nth-of-type(even) .item-container-bottom {
  margin-left: 0; }

.image-selection-container .item-container-wrapper:nth-of-type(odd) .item-container-wrapper-inner {
  padding-right: 10px; }

.image-selection-container .item-container-wrapper:nth-of-type(odd) .item-container,
.image-selection-container .item-container-wrapper:nth-of-type(odd) .item-container-bottom {
  margin-right: 0; }

.image-selection-container .item-container-wrapper-inner {
  width: 100%;
  height: 100%;
  padding: 10px 0; }

.image-selection-container .item-container {
  margin: auto;
  background: white;
  border: 1px solid #CADBEA;
  box-sizing: border-box;
  border-radius: 12px;
  width: 230px;
  height: 200px;
  max-width: 100%;
  max-height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden; }
  @media screen and (max-width: 575px) {
    .image-selection-container .item-container {
      width: 100%; } }
  .image-selection-container .item-container:hover {
    transform: scale(1); }
  .image-selection-container .item-container:active {
    transform: scale(1); }
  .image-selection-container .item-container.selected {
    border: 1px solid #00D7AF; }
  .image-selection-container .item-container.click-left .bg {
    transform-origin: left; }
  .image-selection-container .item-container.click-left .bg-active {
    transform-origin: right; }
  .image-selection-container .item-container.click-right .bg {
    transform-origin: right; }
  .image-selection-container .item-container.click-right .bg-active {
    transform-origin: left; }
  .image-selection-container .item-container .bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 12px;
    background-color: #EAFFF5;
    transform: scaleX(0);
    transition: background-color 0.3s, transform 0.3s ease; }
  .image-selection-container .item-container .bg-active {
    transform: scaleX(1);
    border: 1px solid #00D7AF; }
  .image-selection-container .item-container .image {
    position: relative;
    z-index: 1;
    height: inherit;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center; }

@-moz-document url-prefix() {
  .image-selection-container .item-container .image {
    width: 80%; } }
    .image-selection-container .item-container .image img {
      width: 100%;
      height: auto; }

@-moz-document url-prefix() {
  .image-selection-container .item-container .image img {
    max-width: 150px; } }

.image-selection-container .item-container-bottom {
  position: relative;
  width: 230px;
  max-width: 100%;
  margin: auto;
  background-color: white;
  border: 1px solid #CADBEA;
  box-sizing: border-box;
  border-radius: 12px; }
  .image-selection-container .item-container-bottom .desc {
    position: relative;
    z-index: 1;
    font-weight: bold;
    font-size: 14px;
    text-align: center;
    min-height: 75px;
    max-height: 100%;
    color: #2C3652;
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    @media screen and (max-width: 575px) {
      .image-selection-container .item-container-bottom .desc {
        font-size: 10px;
        padding: 0 8px 10px 8px; } }
    .image-selection-container .item-container-bottom .desc.desc-padding {
      padding-top: 25px;
      justify-content: flex-start; }
    .image-selection-container .item-container-bottom .desc p {
      margin: 0; }
    .image-selection-container .item-container-bottom .desc .sub-title {
      font-weight: normal;
      font-size: 14px;
      text-align: center;
      color: #6E7F95; }
      @media screen and (max-width: 575px) {
        .image-selection-container .item-container-bottom .desc .sub-title {
          font-size: 10px;
          padding: 0 8px; } }
  .image-selection-container .item-container-bottom .inner-tooltip {
    position: absolute;
    right: 10px;
    top: 3px;
    z-index: 2; }
