.persons-container {
  width: 100%;
  border: 1px solid #CADBEA;
  box-sizing: border-box;
  border-radius: 12px;
  padding: 0;
  transition: border 0.3s ease; }

.persons-container .drop-container {
  border: 2px solid #00D7AF; }

.persons-container .text-input {
  padding: 1px;
  flex: 1;
  width: 100%;
  max-height: 18px;
  transform: translateY(22px);
  border: none;
  background-color: transparent;
  font-weight: bold;
  font-size: 12px;
  color: #2C3652; }
  .persons-container .text-input:focus {
    box-shadow: none; }
  .persons-container .text-input::placeholder {
    font-weight: 600;
    font-size: 12px;
    color: #B6BDCB;
    text-overflow: ellipsis; }
  .persons-container .text-input:focus {
    outline: none; }

.persons-container .img-container {
  display: flex;
  align-items: center; }
  .persons-container .img-container img {
    margin: 0 8px 0 15px;
    filter: brightness(0) saturate(100%) invert(85%) sepia(19%) saturate(155%) hue-rotate(182deg) brightness(86%) contrast(91%);
    width: 15px;
    height: 15px; }

.persons-container .drop-arrow {
  position: absolute;
  display: flex;
  justify-content: center;
  height: 100%;
  top: 0;
  right: 22px;
  cursor: pointer; }
  .persons-container .drop-arrow img {
    width: 10px;
    filter: brightness(0) saturate(100%) invert(78%) sepia(45%) saturate(5238%) hue-rotate(121deg) brightness(96%) contrast(101%);
    margin-top: 0px; }
  .persons-container .drop-arrow .angle-up {
    transform: rotate(-180deg); }

@keyframes animate-open {
  0% {
    z-index: 1000; }
  100% {
    z-index: 1000; } }

@keyframes animate-close {
  0% {
    z-index: 1000; }
  100% {
    z-index: 0; } }

.drop-open {
  animation: animate-open 3s infinite; }

.drop-close {
  animation: animate-close 3s linear; }

@keyframes animate-z-visible-v3 {
  0% {
    z-index: -999; }
  1% {
    z-index: 1029; }
  100% {
    z-index: 1029; } }

@keyframes animate-z-invisible-v3 {
  0% {
    z-index: 1029; }
  99% {
    z-index: 1029; }
  100% {
    z-index: -999; } }

@keyframes animate-visible {
  0% {
    transform: scale(0);
    transform-origin: top left; }
  100% {
    transform: scale(1);
    transform-origin: top left; } }

@keyframes animate-invisible {
  0% {
    transform: scale(1);
    transform-origin: top left; }
  100% {
    transform: scale(0);
    transform-origin: top left; } }

.persons-container .drop-container-closed-init {
  transform: scale(0);
  transform-origin: top left;
  z-index: -999; }

.no-internal-desc input {
  max-height: 32px !important;
  transform: translateY(7px) !important; }

.persons-container {
  display: flex;
  align-items: center;
  height: 48px; }
  .persons-container .text-input {
    transform: none;
    display: flex;
    align-items: center;
    cursor: pointer; }
    .persons-container .text-input p {
      margin: 0; }
  .persons-container .drop-container {
    position: absolute;
    top: 120%;
    background: white;
    padding: 18px;
    border-radius: 12px; }
    .persons-container .drop-container:after {
      content: '';
      display: block;
      position: absolute;
      z-index: 0;
      top: -7px;
      left: calc(20% - 6px);
      margin: 0 0 0 -6px;
      width: 12px;
      height: 12px;
      transform: rotate(45deg);
      border-radius: 4px 0 0 0;
      background: white;
      border-left: 2px solid #00D7AF;
      border-top: 2px solid #00D7AF; }
    .persons-container .drop-container .value-container {
      display: flex;
      margin-bottom: 30px; }
      .persons-container .drop-container .value-container:nth-last-of-type(1) {
        margin-bottom: 0; }
      .persons-container .drop-container .value-container p {
        flex: 1;
        padding: 0 32px;
        margin: 0; }
    .persons-container .drop-container .count {
      font-weight: 600;
      font-size: 16px;
      color: #00D7AF;
      transform: translateY(5px); }
    .persons-container .drop-container .desc {
      font-weight: bold;
      font-size: 12px;
      color: #354F53;
      margin-left: 5px;
      margin-top: -5px; }
    .persons-container .drop-container .plus,
    .persons-container .drop-container .minus {
      font-size: 12px;
      transition: transform 0.3s ease; }
      .persons-container .drop-container .plus:active,
      .persons-container .drop-container .minus:active {
        transform: scale(1.5); }
    .persons-container .drop-container .plus {
      color: #00D7AF; }
    .persons-container .drop-container .minus {
      color: #99BBC1; }
  .persons-container .drop-container-closed {
    animation: animate-z-invisible 0.3s linear, animate-invisible 0.3s ease-in-out;
    animation-direction: normal;
    animation-fill-mode: forwards; }
  .persons-container .drop-container-opened {
    animation: animate-z-visible 0.3s linear, animate-visible 0.3s ease-in-out;
    animation-direction: normal;
    animation-fill-mode: forwards; }
