.calender-container {
  width: 100%;
  padding: 0 15px; }
  .calender-container .date-col {
    position: relative;
    width: 14.285714%; }
  .calender-container .day-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2px 0;
    height: 30px;
    flex-wrap: wrap; }
    .calender-container .day-container .day-disabled,
    .calender-container .day-container .day {
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      left: 0; }
    .calender-container .day-container .day:focus {
      outline: none; }
    .calender-container .day-container .day p {
      color: #46737a;
      margin: 0;
      cursor: pointer; }
    .calender-container .day-container .day-active {
      background-color: #00D7AF;
      border-radius: 50%;
      width: 99%; }
    .calender-container .day-container .day-active p {
      color: white; }

@keyframes focus-animation {
  0% {
    background-color: #354F53; }
  50% {
    background-color: #00D7AF; }
  100% {
    background-color: #354F53; } }
    .calender-container .day-container .day-focus {
      animation: focus-animation 3s infinite; }
  .calender-container .day-range {
    height: 30px;
    width: 50%; }
  .calender-container .end-active,
  .calender-container .left-active,
  .calender-container .right-active {
    background: #F2FAF9; }
  .calender-container .end-active {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    position: absolute;
    left: 0; }
  .calender-container .blocked {
    color: #C8E0E4 !important;
    margin: 0; }
