.month-year-selector {
  width: 100%; }
  .month-year-selector .dropdown button {
    width: 100%;
    text-align: left;
    font-size: 12px; }
  .month-year-selector .dropdown-menu {
    padding-top: 0;
    max-height: 200px !important;
    overflow-y: hidden !important;
    transform: translate3d(0px, 36px, 0px) !important;
    min-width: 0 !important; }
    .month-year-selector .dropdown-menu .dropdown-item {
      position: relative;
      z-index: 1;
      height: 100%; }
    .month-year-selector .dropdown-menu .dropdown-item:active {
      background: #36B29B;
      outline: none; }
  .month-year-selector .dropdown-toggle,
  .month-year-selector .btn-outline-secondary:hover {
    background: transparent !important;
    border: solid 2px #C8E0E4; }
  .month-year-selector .btn-outline-secondary:focus,
  .month-year-selector .dropdown-toggle:focus {
    box-shadow: none !important; }
  .month-year-selector .dropdown-toggle:after {
    margin-left: 0.83em !important;
    color: #C8E0E4;
    position: absolute;
    top: 13px;
    right: 10px;
    border: none;
    background-image: url(../../../../assets/images/forms/arrow.svg);
    height: 20px;
    width: 20px;
    background-repeat: no-repeat;
    background-size: 15px; }
